.selector{margin-top:24px;margin-left:32px;margin-right:64px;width:297px}.selector select{width:100%;height:44px;border:1px solid #d8d8d8;border-radius:3px;padding-left:16px;padding-right:20px;appearance:none;position:relative;background:url(../../img/bg.svg);background-repeat:no-repeat;background-size:6px 4px;background-position:right 16px center}.selector select:after{content:"";width:6px;height:4px;position:absolute;right:16px;top:calc(50% - 2px)}.ranger{margin-top:24px;margin-left:32px;margin-right:64px;width:100%;display:flex;flex-direction:column;gap:23px}.ranger .range{position:absolute;bottom:-7px !important}.ranger .number{position:absolute;bottom:21px;left:0;width:53%;text-align:center;font-weight:500;font-size:15px;line-height:135%;text-align:center;letter-spacing:.03em;color:#bea257}.ranger .number2{position:absolute;bottom:21px;right:0;width:53%;text-align:center;font-weight:500;font-size:15px;line-height:135%;text-align:center;letter-spacing:.03em;color:#bea257}.ranger .input-range__track--active{background:#bea257}.ranger .input-range__slider{-webkit-appearance:none;-moz-appearance:none;appearance:none;background:#bea257;border:none;border-radius:100%;cursor:pointer;display:block;height:13px;margin-left:-0.5rem;margin-top:-0.65rem;outline:none;position:absolute;top:50%;transition:transform .3s ease-out,box-shadow .3s ease-out;width:13px}.ranger__item{width:297px;height:50px;border:1px solid #eee;border-radius:3px;display:flex;align-items:center;justify-content:center;position:relative}.ranger__item .input-range{height:1rem;position:relative;width:240px !important}.ranger__item .range{position:absolute;bottom:0}.ranger__item .input-range__label-container{display:none}.ranger__item span{font-weight:500;font-size:15px;line-height:135%;display:flex;align-items:center;text-align:center;letter-spacing:.03em;color:#bea257}.ranger__item.from::before{content:"от";font-size:15px;line-height:135%;position:absolute;display:flex;align-items:center;letter-spacing:.